@import url('https://fonts.googleapis.com/css2?family=Kalnia:wght@600&family=Montserrat&display=swap');

.container{
  padding: 120px 150px 0px 130px;
}

.start-info{
  display: flex;
  gap: 40px;
  justify-items: center;
  justify-content: center;
}

.me_img{
  width: 350px;
  height: max-content;
  border-radius: 20px;
}

.text-info{
  display: flex;
  flex-direction: row;
  gap: 50px;
}

.text{
  display: flex;
  flex-direction: column;
  gap: 0px;
  flex-wrap: wrap;
}

.title-1{
  font-family: 'Kalnia', serif;
  font-size: 80px;
  margin-top: 15px;
  margin-bottom: 0px;
}

.title-2{
  font-family: 'Kalnia', serif;
  font-size: 80px;
  margin: 0;
  padding-left: 90px;
  margin-bottom: 25px;
}

.text p{
  margin-bottom: 10px;
  font-family: 'Montserrat', sans-serif;
  font-size: 17px;
  color: #4b4a4a;
}

.buttons{
  margin-top: 35px;
}

.buttons button{
  width: 190px;
  height: 60px;
  font-size: 20px;
  background-color: cornflowerblue;
  border-radius: 40px;
  border: #ffffff;
}

.buttons :hover{
  transform: translate(10px, 0px);
  color: black;
}

.button_text{
  text-decoration: auto;
  color: #fff;
  font-family: 'Montserrat', sans-serif;
}

.about-info{
  margin-top: 100px;
}

.about-info h2{
  font-family: 'Kalnia', serif;
  font-size: 60px;
  margin-top: 15px;
  margin-bottom: 0px;
}

.info{
  display: grid;
  grid-template-columns: 30% 50% 40%;
}

.education, .skills, .languages{
  display: flex;
  font-family: 'Montserrat', sans-serif;
  flex-direction: column;
  justify-self: center;
  margin-top: 25px;
}

.education h3, .skills h3, .languages h3{
  font-size: 25px;
  font-weight: bold;
}

.education p, .skills p, .languages p{
  display: flex;
  font-size: 20px;
  flex-wrap: wrap;
  margin: 0;
}

.projects{
  display: flex;
  flex-direction: column;
  margin-top: 100px;
}

.projects1{
  display: flex;
  flex-direction: column;
  margin-top: 0px;
}

.projects h2{
  font-family: 'Kalnia', serif;
  font-size: 60px;
  margin-top: 15px;
  margin-bottom: 0px;
  width: 600px;
}

.projects-title{
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 300px;
}

.projects-title button{
  width: 190px;
  height: 60px;
  font-size: 20px;
  background-color: cornflowerblue;
  border-radius: 40px;
  border: #ffffff;
}

.projects-title button :hover{
  transform: translate(10px, 0px);
  color: black;
}

.projects-list{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 30px;
  justify-content: space-between;
  margin-top: 50px;
}

.project{
  display: flex;
  flex-direction: row;
  border-radius: 25px;
  border: #4b4a4a solid 1px;
  width: 500px;
  height: 250px;
  padding: 20px;
  align-items: center;
  justify-content: space-between;
}

.project-description{
  display: flex;
  flex-direction: column;
  width: 200px;
}

.project-description h2{
  font-family: 'Montserrat', sans-serif;
  font-size: 25px;
  margin: 0;
}

.project-description p{
  display: flex;
  font-family: 'Montserrat', sans-serif;
  font-size: 20px;
  flex-wrap: wrap;
  width: 250px;
}

.project-description a{
  color: black;
}

.projects-img{
  width: 200px;
  height: max-content;
}

.offers{
  margin-top: 100px;
}

.offers h2{
  font-family: 'Kalnia', serif;
  font-size: 60px;
  margin-top: 15px;
  margin-bottom: 0px;
}

.offers-list{
  display: grid;
  margin-top: 15px;
  grid-template-columns: 35% 34% 35%;
  grid-column-gap: 4%;
}

.offer{
  display: flex;
  flex-direction: column;
  justify-items: center;
}

.offer h2{
  font-family: 'Montserrat', sans-serif;
  font-size: 25px;
}

.offer p{
  font-family: 'Montserrat', sans-serif;
  font-size: 20px;
}

@media only screen and (max-width: 600px) {
  .about h2 {
    font-size: 40px;
  }

  .about .prompt {
    margin-top: 10px;
    font-size: 20px;
    width: 80%;
  }

  .skills {
    text-align: center;
  }
  .list {
    padding: 0;
  }

  .skills h2 {
    font-size: 30px;
  }
}
