@import url('https://fonts.googleapis.com/css2?family=Kalnia:wght@600&family=Montserrat&display=swap');


.footer{
  display: flex;
  flex-direction: column;
  gap: 10px;
  font-family: 'Montserrat', sans-serif;
  font-size: 20px;
  align-items: center;
  background-color: cornflowerblue;
  margin-top: 100px;
  padding-bottom: 20px;
  padding-top: 20px;
}

.footer-p{
  margin-top: 40px;
  display: flex;
  gap: 10px;
  flex-direction: column;
}

.footer-p p{
  margin: 0;
}

.user-contacts h2{
  font-family: 'Kalnia', serif;
  font-size: 60px;
  margin-top: 15px;
  margin-bottom: 0px;
  text-align: center;
}

.contacts{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 30px;
  gap: 15px;
}

.contact{
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 10px;
}

.contact h4{
  font-family: 'Montserrat', sans-serif;
  font-size: 25px;
  margin: 0;
}

.icons{
  width: 40px;
}

@media only screen and (max-width: 600px) {
  .footer svg {
    font-size: 50px;
  }
}
